html {
	margin: 0;
  height: 100%;
  font-family: sans-serif;
}

body {
  margin: 0;
	height: 100%;
}

#root {
  height: 100%;
  width: 100%;
  display: table;
}

p {
	font-weight: 100;
}

h1 {
	font-weight: 100;
}

.title {
  font-size: 2em;
  border: none;
  margin: 0;
}

input {
	width: 40%;
	min-width: 220px;
	height: 2em;
	border: 1px solid rgba(0,0,0,0);
	-webkit-box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.28);
	        box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.28);
	padding: 4px 20px 2px 20px;
	line-height: 2em;
	font-size: 1em;
}

input:focus {
	outline: none;
}

.words {
	padding: 5%;
}



